nav {
    margin: 0;
    padding: 0;
    position: fixed;
    z-index: 3;
    background-color: white;
    width: 100%;

    .navbar {
        padding: 0 20px;
        display: flex;
        justify-content: space-between;
        position: relative;

        .header {
            display: grid;
            place-content: center;

            img {
                width: 20%;
            }
        }

        .tools {
            display: flex;
            gap: 20px;

            ul {
                display: flex;
                gap: 25px;
                list-style-type: none;

                li {
                    font-size: 18px;
                    font-weight: 600;

                }
            }
        }

        .menu {
            display: grid;
            place-content: center;

            .icon {
                font-size: 27px;
            }
        }

        .search {
            display: none;
        }

        .icons {
            ul {
                display: flex;
                list-style-type: none;
                gap: 25px;

                li {
                    text-decoration: none;

                    .icon {
                        font-size: 27px;
                    }

                    Link {
                        color: rgb(155, 128, 9);
                    }

                    .iconss {
                        color: rgb(155, 128, 9);
                    }

                }

                .home {
                    display: none;
                }
            }
        }
    }

    @media (max-width:1160px) {

        .navbar {
            padding: 0 20px;
            display: flex;
            justify-content: space-between;
            position: relative;

            .header {
                position: absolute;
                left: 48%;

                img {
                    display: grid;
                    place-content: center;
                    width: 20%;
                    margin-top: 5px;
                }
            }

            .tools {
                display: flex;
                gap: 20px;

                .tool {
                    display: none;
                    gap: 30px;
                    list-style-type: none;

                    li {
                        font-size: 18px;
                        font-weight: 600;
                    }
                }
            }

            .menu {
                display: grid;
                place-content: center;
            }

            .search {
                display: none;
            }

            .icons {
                ul {
                    display: flex;
                    list-style-type: none;
                    gap: 25px;

                    li {
                        text-decoration: none;
                        font-size: 30px;
                    }

                    .home {
                        display: none;
                    }
                }
            }
        }

    }


    @media (max-width:699px) {

        .navbar {
            padding: 10px 20px;
            display: flex;
            justify-content: space-between;
            position: relative;
            height: fit-content;
            // gap: 10px;

            .header {
                position: absolute;
                left: 45%;
                top: 0px;
                height: fit-content;

                img {
                    display: grid;
                    place-content: center;
                    width: 20%;
                }
            }

            .tools {
                display: flex;
                gap: 20px;

                .tool {
                    display: none;
                    gap: 30px;
                    list-style-type: none;

                    li {
                        font-size: 18px;
                        font-weight: 600;
                    }
                }
            }

            .menu {
                display: grid;
                place-content: center;
            }

            .search {
                display: block;
                width: fit-content;
                position: relative;

                .s-icon {
                    font-size: 30px;
                    color: rgb(155, 128, 9);
                    display: grid;
                    place-content: center;
                }
            }

            .icons {
                position: fixed;
                bottom: 0px;
                left: 0px;
                width: 100%;
                background-color: rgb(245, 239, 239);
                z-index: 3;
                border-radius: 20px 20px 0 0;

                ul {
                    padding: 7px 20px;
                    margin: 0px;
                    display: flex;
                    gap: 0px;
                    list-style-type: none;
                    justify-content: space-evenly;

                    li {
                        text-decoration: none;
                    }

                    .home {
                        display: block;
                        background-color: rgb(245, 239, 239);
                    }

                    .searchs {
                        display: none;
                    }
                }
            }
        }

    }


}