.slides {
    box-sizing: border-box;
    padding-top: 80px;
    margin-bottom: 20px;
    // border: 1px solid black;
    // border: ;


    .heroslider {
        // padding: 20px 0;
        // border-top: 1px solid #000;
        // border-bottom: 1px solid #000;
        // border: 1px solid black;

        .slide {}

        .image {
            height: 100%;
            width: 100%;
            text-align: center;

            img {
                height: 100%;
                width: 100%;
                text-align: center;
                object-fit:scale-down;
            }
        }
    }



}

@media (max-width:500px) {

    .slides {
        padding-top: 10px;
        margin-bottom: -30px;

        .heroslider {
            padding: 0;
            // width: fit-content;
            max-height: fit-content;

            .slide {}

            .image {
                width: 100%;
                height: 100%;
                text-align: center;
                justify-content: center;
                place-content: center;

                img {
                    height: 100%;
                    width: 100%;
                    text-align: center;
                    object-fit: contain;
                }
            }
        }
    }


}